import React from 'react'
import "./style.css"

function Navbar() {
  return (
    <>
      <nav className="navbar">
      <div>
        <i className="fas fa-headphones-alt navicon fa-2x"></i>
        <span className="navtext">React Tracks</span>
      </div>
      <div>
        <i className="fas fa-user-secret fa-2x"></i>
        <span className="navtext"> Yash</span>
      </div>
      <div>
        <span className="navtext">Sign Out</span>
        <i className="fas fa-sign-out-alt fa-2x"></i>
      </div>
    </nav>
    </>
  )
}

export default Navbar
