import React from 'react'
import Data from './Data'

function Music(){
  return (
    <>
    {Data.map((song)=>{
      return <>
      <div className="music" key={song.id}>
        <div className="likes">
          <span>{song.like}</span>
          <button><i className="fas fa-thumbs-up fa-2x likeicon"></i></button>
        </div>

         <div className="songdetails">
          <p className="title">{song.title}</p>
          <p className="subtitle">{song.subtitle}</p> 
        </div>

        <div className="controls">
          <button><i className="fas fa-play"></i></button>
          <span>0:00/0:00</span>
          <input type="range" min="0" max="100" value="0" id="" />
          <button><i className="fas fa-volume-up"></i></button>
          <button><i className="fas fa-ellipsis-v"></i></button>
          <button id="close"><strong>V</strong></button>
        </div>
      </div>
      </>
    })};
      
    </>
  )
}

export default Music


